*{
    box-sizing:border-box;
}

*{
    box-sizing: border-box;
}

body{
    margin:0;
    padding:0;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    
    background-color:#eff3f7;
    height:100%;
}
html, body, #potato,.container{
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader{
    width: 100%;
    height: 100%;  
    display:flex;
    justify-content: center;
    align-self: center;
    font-weight:300;
}
.movies{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap:wrap;
    padding: 50px;
    paddinig-top: 70px;
    width:80%;
}
.movies .movie{
    width:45%;
    background-color: white;
    margin-bottom:70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-family: 300;
    padding:20px;
    color:#adaeb9;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img{
    position: relative;
    top:-50px;
    max-width: 180px;
    width:100%;
    margin-right: 30px;
    box-shadow:0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    border-radius: 3px;
}

.movie .movie__title,
.movie .movie__year{
    margin:0;
    font-family: 300;
    color:#2c2c2c;
}

.movie h3{
    margin-bottom: 5px;
    font-size: 24px;
}
.movie .movie__genres{ 
    list-style: none;
    padding: 0;
    margin:0;
    display: flex;
    margin:5px 0px;
}

.movie__genres li{
    margin-right:10px;
    font-size: 14px;
}